<template>
  <div class="page-home">
    <div v-for="item in list">
      <com-pic :src="item"></com-pic>
    </div>
  </div>
</template>

<script>
import Tooler from '../js/tooler'
import ComPic from './com-pic'

export default {
  data () {
    return {
      list: Tooler.getPicList(1, 120, "z")
    }
  },
  components:{ComPic},
  methods:{
    getUrl(n){
      return "static/asset/img/z" + n + ".jpg";
    }
  }
}
</script>

<style scoped>
.page-home{
  font-size: 0;
  display: flex;
  flex-wrap: wrap;
}
</style>



